body {
  font-family: $font-oswald;
  font-size: 14px;
  line-height: 20px;
  color: $color-black;
  margin: 0 auto;
}

a {
  font: inherit;
  text-decoration: none;
  color: inherit;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
